.dashboard-main-nav{width:100%;display:grid;grid-template-columns:repeat(6, 1fr);border:0.1rem solid #464646}@media screen and (max-width: 1500px){.dashboard-main-nav{grid-template-columns:repeat(3, 1fr);border-bottom:none}}@media screen and (max-width: 767px){.dashboard-main-nav{grid-template-columns:1fr;border-bottom:none}}.dashboard-main-nav .dashboard-nav-list-item{height:100%;width:100%}.dashboard-main-nav .dashboard-nav-list-item:not(:last-child){border-right:0.1rem solid #464646}@media screen and (max-width: 1500px){.dashboard-main-nav .dashboard-nav-list-item{border-bottom:0.1rem solid #464646}}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link{width:100%;height:100%;padding:3.5rem 0;display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;transition:var(--hover-transition)}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link:hover{background-color:#464646}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link .label-medium{font-weight:400}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link.selected{background-color:#fafafa}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link.selected .label-medium{color:#1a1a1a}

.dashboard-kin-update{position:relative;height:100%;padding:10rem 12rem}@media screen and (max-width: 1366px){.dashboard-kin-update{padding:10rem 10rem}}@media screen and (max-width: 1023px){.dashboard-kin-update{padding:10rem 9rem}}@media screen and (max-width: 768px){.dashboard-kin-update{padding:10rem 8rem}}@media screen and (max-width: 650px){.dashboard-kin-update{padding:5rem 4rem}}.dashboard-kin-update::after{content:'';position:absolute;bottom:0;left:0;right:0;width:100%;height:0.1rem;background-color:#464646}.dashboard-kin-update-inner{max-width:85%;margin:0 auto;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}@media screen and (max-width: 650px){.dashboard-kin-update-inner{width:100%;max-width:100%}}.dashboard-kin-update-inner .dashboard-form-block{width:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;padding-top:var(--general-padding-medium)}.dashboard-kin-update-inner .dashboard-form-block .dashboard-form-block-heading-wrapper{width:100%;height:auto;margin-bottom:var(--margin-large);overflow:hidden}@media screen and (max-width: 650px){.dashboard-kin-update-inner .dashboard-form-block .dashboard-form-block-heading-wrapper{margin-bottom:var(--margin-medium);text-align:center}}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form{width:100%;display:grid;grid-template-columns:repeat(2, 1fr);grid-auto-flow:row;grid-column-gap:10rem;-webkit-column-gap:10rem;column-gap:10rem;margin-bottom:var(--margin-medium)}@media screen and (max-width: 1300px){.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form{grid-template-columns:1fr}}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-block{height:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group{-webkit-flex:1 1;flex:1 1;width:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start;margin-bottom:var(--margin-medium)}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .form-group-label{margin-bottom:1.5rem}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .input-wrapper{position:relative;width:100%}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .input-wrapper .show-password-button{position:absolute;top:0;right:0;height:100%;width:auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:0 2.5rem;background-color:transparent}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .form-group-input{width:100%;padding:2.5rem;border:0.1rem solid #464646;transition:var(--hover-transition)}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .form-group-input::-webkit-calendar-picker-indicator{-webkit-filter:invert(1);filter:invert(1)}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .form-group-input:hover,.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .form-group .form-group-input:focus{border-color:#fafafa}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .button-primary{grid-column:1 / -1;justify-self:center}.dashboard-kin-update-inner .dashboard-form-block .dashboard-kin-update-form .button-primary:not(:last-child){margin-bottom:var(--margin-small)}

